"use client"
import { ConnectButton } from "@iota/dapp-kit"
import { useWallet } from "./WalletContext";
import { useState } from "react";

export function WalletConnect() {
  const { wallet, setWallet } = useWallet();
  const [manualAddress, setManualAddress] = useState("");

  // N·∫øu sau n√†y c√≥ hook l·∫•y address t·ª´ dapp-kit th√¨ S·ª¨A ƒêO·∫†N N√ÄY l·∫°i l·∫•y t·ª´ hook.
  const handleSetManualAddress = () => {
    if (manualAddress) {
      setWallet({ ...wallet, address: manualAddress });
      setManualAddress("");
    }
  };

  return (
    <div style={{ padding: "1rem", display: "flex", justifyContent: "flex-end", gap: 16 }}>
      <ConnectButton />
      {/* N·∫øu l·∫•y ƒë∆∞·ª£c address th·∫≠t t·ª´ dapp-kit, show ·ªü ƒë√¢y lu√¥n */}
      {wallet.address ? (
        <span style={{ color: "#333", background: "#eee", borderRadius: 24, padding: "6px 14px", fontWeight: 600 }}>
          {wallet.address.slice(0,8)}...{wallet.address.slice(-4)}
        </span>
      ) : (
        <>
          {/* TEST: Cho nh·∫≠p address ƒë·ªÉ l·∫•y tr·∫°ng th√°i connect */}
          <input
            value={manualAddress}
            placeholder="Nh·∫≠p v√≠ test"
            onChange={e=>setManualAddress(e.target.value)}
            style={{borderRadius:12, padding:6}}
          />
          <button onClick={handleSetManualAddress}>X√°c nh·∫≠n</button>
        </>
      )}
    </div>
  );
}